@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500;600&display=swap);
body {
    margin: 0;
    background-color: rgb(153, 51, 153);
    color: rgb(255, 255, 255);
    font-family: 'Orbitron', sans-serif;
}

.Play {
    text-align: center;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
}

.board-wrapper {
    padding: 30px;
    display: flex;
    justify-content: center;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 8px;
    box-shadow: 1px 1px 25px 0px rgba(50, 50, 50, 0.79);
}

.winner-wrapper {
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 8px;
    box-shadow: 1px 1px 25px 0px rgba(50, 50, 50, 0.79);
}

.display-wrapper {
    display: flex;
    flex-direction: column;
    justify-self: start;
    align-items: left;
    margin-right: 50px;
}

table {
    background-color: rgb(231, 154, 255);
    border: 1px solid rgb(255, 255, 255);
    border-radius: 2px;
    border-spacing: 0;
    box-shadow: 1px 1px 25px 0px rgba(50, 50, 50, 0.79);
    border-spacing: 0px;
}

table td {
    width: 25px;
    height: 25px;
    border-radius: 2px;
    margin: 0;
    padding: 0;
    border: 1px solid rgb(255, 255, 255);
}

.snake {
    z-index: 5;
    background-image: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);
    box-shadow: 1px 1px 25px 0px rgba(50, 50, 50, 0.79);
}

.ananas {
    background-image: url(/github.io/static/media/ananas.83bb23a4.png);
    background-size: cover;
    z-index: 1;
}

